.main__container-row-areas-work {
    width: 1150px;
    margin: 0 auto;
    padding-top: 100px;
    padding-bottom: 65px;
    background: url('../img/main-areas-work-background.jpg') 100% 95px no-repeat;
}
.container-row-areas-work__description-directions {
    font-family: 'Noto Sans', sans-serif;
    max-width: 650px;
}
.description-directions__title-appellation {
    font-size: 20px;
    color: $main-areas-work-text;
}
.description-directions__title {
    font-size: 44px;
    line-height: 70px;
    font-weight: 400;
    color: $main-areas-work-text;
    margin: 0;
}
.description-directions__information {
    font-size: 16px;
    line-height: 26px;
    margin: 0;
}
.container-row-areas-work__directions-list {
    max-width: 665px;
    padding-top: 10px;
    font-family: 'Noto Sans', sans-serif;
    position: relative;
}
.directions-list__title {
    font-size: 20px;
    line-height: 55px;
    color: $main-areas-work-text;
    font-weight: 400;
    margin: 0;
}
.directions-list__list-areas-work {
    list-style-type: none;
    background: url('../img/line-list.png') 0 50% no-repeat;
    padding-left: 20px;
    margin: 0;
}
.list-areas-work__link {
    display: inline-block;
    text-decoration: none;
    border-bottom: 1px solid $main-areas-work-link-uderline-color;
    padding-right: 20px;
    color: $main-areas-work-link-color;
    line-height: 23px;
    padding-top: 3px;
}
.list-areas-work__link:first-child {
    padding-top: 0;
}
.directions-list__link-catalog {
    display: flex;
    width: 320px;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    right: 0;
    bottom: 0;
}
.link-catalog__arrows {
    float: left;
    width: 61px;
    height: 40px;
    background: url('../img/arrow.png') 50% 50% no-repeat;
}