.main__container-row-information {
    height: 680px;
    position: relative;
}
.container-row-information__container-position {
    position: absolute;
    right: 20px;
    top: 120px;
    max-width: 660px;
}
.container-position__information {
    margin: 0;
    margin-bottom: 60px;
    font-size: 44px;
    line-height: 52px;
    color: $main-information-text-color;
    font-family: 'Noto Sans', sans-serif;
}
.information__allotment {
    margin-top: 20px;
    font-size: 22px;
    background-color: $main-information-allotment-bg-color;
    color: $main-information-allotment-color;
    display: block;
    max-width: 540px;
    padding-left: 20px;
}