.main__customer-services {
    background-color: $main-customer-services-bg-color;
    padding-top: 82px;
    padding-bottom: 220px;
    margin-bottom: 700px;
    font-family: 'Noto Sans', sans-serif;
}
.customer-services__scroll-services-clients {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 20px;
}
.scroll-services-clients__item-services-clients {
    padding-left: 70px;
    position: relative;
    width: 30%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    color: $main-scroll-services-clients-color;
    font-size: 15px;
    line-height: 20px;
    padding-bottom: 20px;
}
.scroll-services-clients__item-services-clients_compilation::after {
    content: '';
    display: block;
    width: 70px;
    height: 70px;
    position: absolute;
    left: 0;
    top: 0;
    background: url("../img/sprite.png") -38px -85px no-repeat;
}
