.container-row-areas-work__slider {
    display: flex !important;
    align-items: center;
    justify-content: space-around;
    padding-top: 45px;
}
.slick-slide {
    display: flex !important;
    height: 140px;
    align-items: center;
    justify-content: space-around;
    outline: none;
}
.slider__arrow {
    background: url('../img/slider-arrow.png') 50% 50% no-repeat;
    width: 14px;
    height: 25px;
}
.slider__arrow_next {
    transform: rotate(180deg);
}
.slick-list {
    width: 1060px;
}