.header {
    background-color: $header-color;
    font-family: 'Noto Sans', sans-serif;
}
.header__nav {
    float: left;
}
.nav__link {
    text-decoration: none;
    font-size: 16px;
    line-height: 50px;
    color: $header-color-default;
    display: inline-block;
    float: left;
    margin-right: 25px;
}
.nav__link_active::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 3px;
    background-color: $header-color-active;
}
.nav__link_active {
    position: relative;
    color: $header-color-active;
}
.header__switch-language {
    float: right;
    display: flex;
    align-items: center;
    height: 50px;
}
.switch-language__tongue {
    cursor: pointer;
    font-size: 13px;
    text-transform: uppercase;
    display: block;
    float: left;
    border-radius: 50%;
    margin-left: 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
}
.switch-language__tongue_rus {
    background-color: $header-color-rus;
    color: $header-color-rus-text;
}
.switch-language__tongue_eng {
    background-color: $header-color-eng;
    color: $header-color-eng-text;
}