.main__information-about-us {
    width: 950px;
    height: 690px;
    box-sizing: border-box;
    padding: 60px 0 60px 100px;
    margin: 0 auto;
    position: absolute;
    top: -805px;
    left: 50%;
    transform: translateX(-50%);
    background: #fff url('../img/main-bg-about-us.png') 50% 50% no-repeat;
    background-size: contain;
    border-top: 3px solid $main-information-about-us-color;
    box-shadow: 0 0 30px 0 #dad9d9;
    font-family: 'Noto sans', sans-serif;
    color: $main-information-about-us--text-color;
}
.main__information-about-us::before {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 1107px;
    height: 744px;
    background: url('../img/bg-about-us.png') 50% 50% no-repeat;
    background-size: contain;
}
.main__information-about-us::after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    bottom: -56px;
    width: 190px;
    height: 55px;
    overflow: hidden;
    background: url('../img/bottom-bg-about-us.png') 50% 92% no-repeat;
}
.information-about-us__title-appellation {
    font-size: 20px;  
}
.information-about-us__title {
    font-size: 44px;
    margin: 10px 0;
    margin-bottom: 35px;
}
.information-about-us__description {
    font-size: 14px;
    line-height: 26px;
    margin-bottom: 20px;
}
.description__link {
    color: $main-information-about-us-color;
}