.button {
    cursor: pointer;
    font-family: 'Noto Sans', sans-serif;
    box-sizing: border-box;
    width: 180px;
    background-color: $buttons-color;
    border: 2px solid $buttons-border-color;
    border-radius: 25px;
    color: $buttons-text-color;
    line-height: 38px;
    text-align: center;
    padding: 0 20px;
}
.button_size {
    width: 240px;
    line-height: 65px;
    border-radius: 30px;
    font-size: 16px;
}
.button_text-color {
    color: $button_text-color;
}
.button_bg-color {
    border: none;
    background-image: linear-gradient(0.05turn, #2d4ed0, #9a69af);
    box-shadow: 0 0 44px 0px #2d4ed0;
}
.button_no-shadow {
    box-shadow: none;
}