.main__container-row-communication {
    padding-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.main__logo {
    width: 270px;
    height: 65px;
    background: url('../img/logo.png') 50% 50% no-repeat;
}
.search__entry-field {
    outline: none;
    width: 460px;
    line-height: 40px;
    border: none;
    border-radius: 25px;
    font-family: 'Noto Sans', sans-serif;
    font-size: 16px;
    padding-left: 20px;
    background: $main-color url('../img/search.png') 95% 50% no-repeat;
}
.main__communication {
    text-align: right;
    max-width: 175px;
}
.communication__element {
    display: inline-block;
    width: 100%;
    position: relative;
    color: $main-color;
    font-family: 'Noto Sans', sans-serif;
    font-size: 16px;
    line-height: 27px;
}
.communication__element_tel::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 27px;
    background: url('../img/sprite.png') -32px -31px no-repeat;
}
.communication__element_email::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 27px;
    background: url('../img/sprite.png') -76px -31px no-repeat;
}
